header {
  display: -ms-flexbox;
  display: flex;
  height: 70px;
  /* justify-content: center; */
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #afc5ed;
}

.header-desktop {
  -ms-flex-pack: center;
      justify-content: center;
}

.header-mobile {
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.book-search-header {
  color: #255cc4;
}

.book-search-header-mobile {
  color: #255cc4;
  margin-left: 10px;
}

.book-search-header-mobile span {
  display: inline-block;
  -webkit-transform: translateX(3px);
      -ms-transform: translateX(3px);
          transform: translateX(3px);
}

.arrow-down {
  width: 25px;
  height: 25px;
  border-top: 5px solid #255cc4;
  border-right: 5px solid #255cc4;

  -webkit-transform: rotate(135deg) translateY(10px);

      -ms-transform: rotate(135deg) translateY(10px);

          transform: rotate(135deg) translateY(10px);

  margin-right: 10px;
}



